import React from 'react';

import { Helmet } from 'react-helmet';
import { Container, Row, Col } from 'react-bootstrap';

import CardsInfo from './CardsInfo';
import CardsPricing from './CardsPricing';

function Home() {
  return (
    <div>
      <Helmet>
        <title>Home - KlayHosting</title>
      </Helmet>

      <div className="pt-5 pb-2 contentContainer text-center" id="headerMainText">
        <span className="h1 text-white" id="headerText">
            Host here your<br />
            Discord Bot for <span style={{ color: '#FD794F' }}>Free</span><br />
            by Docker technology
        </span>
      </div>

      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 101"><path fill="#1A1A1A" fillOpacity="1" d="M0,100L1440,0L1440,0L0,0Z"></path></svg>

      <div>
        <h2 className="mt-5 text-center subTitle">Why choose us?</h2>
        <hr className="mx-auto" style={{ width: '5%' }} />
        <Container>
          <Row>
            <Col>
              <CardsInfo title="Security" content="All our systems are based on a solid and secure certified network structure to guarantee you the greatest security." />
            </Col>
            <Col>
              <CardsInfo title="Guaranteed uptime " content="Thanks to the chosen infrastructures we are able to guarantee top uptime on an annual basis (99.97%)." />
            </Col>
            <Col>
              <CardsInfo title="Competitive prices" content="We offer the best service at an affordable price compared to our competitors on the market." />
            </Col>
          </Row>
        </Container>

        <h2 className="mt-5 text-center subTitle">Our Plan</h2>
        <hr className="mx-auto" style={{ width: '5%' }} />

        <Container className="mt-5 mb-5">
          <Row>
            <Col>
              <CardsPricing title="Free" price="0" desc="Perfect for small private bots, development bots, or if you're just starting out." ram="128" disk="512" db="1" backup="1" />
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  )
}

export default Home;