import React from 'react';

import { Card, ListGroup, ListGroupItem, Button } from 'react-bootstrap';

function CardsPricing(props) {
  return (
    <div>
      <Card text='white' className="mx-auto cardEdit cardPrcing text-center" style={{ width: '97%', backgroundColor: '#1A1A1A' }}>
        <Card.Body>
          <Card.Title>{props.title}</Card.Title>
          <Card.Text style={{ color: '#FD794F' }}><span style={{ fontSize: '25px', verticalAlign: 'top', position: 'relative', top: '0.5em' }}>€</span> <span style={{ fontSize: '50px' }}>{props.price}</span> <span style={{ fontSize: '20px' }}>/mo</span></Card.Text>
          <Card.Text>{props.desc}</Card.Text>
        </Card.Body>
        <ListGroup className="list-group-flush">
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'grey', color: 'lightgrey' }}>{props.ram}mb RAM</ListGroupItem>
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'grey' , color: 'lightgrey'}}>{props.disk}mb SSD</ListGroupItem>
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'grey', color: 'lightgrey' }}>{props.db}x MySQL Database</ListGroupItem>
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'grey', color: 'lightgrey' }}>{props.backup}x Backup</ListGroupItem>
        </ListGroup>
        <Card.Body>
          <Card.Link href="https://billing.klayhosting.com/servers"><Button variant="light">Redeem now</Button></Card.Link>
        </Card.Body>
      </Card>
    </div>
  )
}

export default CardsPricing;